import { useEffect, useRef, useState } from 'react';
import projectList from './projectList'
import Modal from './Modal'

interface ProjectProps {
    language: boolean;
    setLanguage: React.Dispatch<React.SetStateAction<boolean>>;
}

const Project: React.FC<ProjectProps> = (props) => {
	useEffect(()=>{
		console.log('Project rerendering')
	},[])
	const imageUrls :string[] = []; // 로드할 이미지 URL 목록 array

	projectList.forEach((el, i)=>{
		imageUrls.push(`${process.env.PUBLIC_URL}/img/jpg/${el.rsc.name}_${el.responsive.main}.jpg`)
	})

	let [isLoading, setIsLoading] = useState(true); // 모든 이미지가 로드되었는지 여부를 추적하는 상태 변수
	let [modal, setModal] = useState(false);
	let [target, setTarget] = useState(0);
  
	useEffect(() => {
		const imageLoadStatus = Array(imageUrls.length).fill(true); // 각 이미지의 로딩 상태를 추적하는 배열 생성
		let loadedImageCount = 0; // 로드된 이미지의 개수를 추적하는 변수
	
		const checkAllImagesLoaded = () => {
			// 모든 이미지가 로드되었는지 확인
			if (loadedImageCount === imageUrls.length) {
				setIsLoading(false);
			}
		};
	
		const handleImageLoad = (index:number) => {
			// 이미지가 로드될 때 호출되는 콜백 함수
			imageLoadStatus[index] = true; // 해당 이미지의 로딩 상태를 true로 변경
			loadedImageCount++; // 로드된 이미지 개수 증가
			checkAllImagesLoaded(); // 모든 이미지가 로드되었는지 확인
		};
	
		// 각 이미지의 로드를 감지하기 위해 onload 이벤트 핸들러 추가
		imageUrls.forEach((imageUrl, index) => {
			const image = new Image();
			image.onload = () => handleImageLoad(index);
			image.src = imageUrl;
		});
	
		// 컴포넌트가 언마운트될 때 이벤트 리스너 제거
		return () => {
			// 이미지 요소의 onload 이벤트 핸들러 제거
			imageUrls.forEach((imageUrl, index) => {
			const image = new Image();
			image.onload = null;
			});
		};
	}, [imageUrls]); // imageUrls 배열이 변경될 때마다 useEffect 재실행
	return(
		<div className="container">
			{ isLoading && <aside className="msg-cover loading"><img src={`${process.env.PUBLIC_URL}/img/icon-loading.gif`} alt="" /></aside> }
			{
				modal
				?<Modal modal={modal} setModal={setModal} target={target} setTarget={setTarget} language={props.language} setLanguage={props.setLanguage}/>
				:null
			}
			<h1>Project</h1>
			<section>
				<ul className="pj">
					{
						projectList.map((el, i)=>{
							return(
								<li key={i}>
									<a href="#" onClick={(e)=>{
										e.preventDefault();
										setModal(!modal)
										setTarget(i)
									}}>
										<img src={`${process.env.PUBLIC_URL}/img/webp/${el.rsc.name}_${el.responsive.main}.webp`} alt=""/>
										<div className="cover"></div>
										<div className="desc">
											<p className="pro-tit">
												{
													props.language
													? el.projectName.eng
													: el.projectName.kor
												}
											</p>
											<div className="tags">
												{
													el.skills.map((element, j)=>{
														return(
															<span className="tag" key={j}>{element}</span>
														)
													})
												}
											</div>
										</div>
									</a>
								</li>
							)
						})
					}
				</ul>
			</section>
		</div>
	)
}

export default Project;